body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightskyblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  color: orangered;
}

h4 {
  color: orangered;
}

li {
  color: black;
  padding-top: 3px;
  font-size: 20px;
  font-weight: bold;
}

.deletor {
  margin-left: 10px;
  color: orangered;
}

input {
  margin-left: 10px;
  background-color: lightsteelblue;
}

div {
  margin-left: 2px;
}

.error {
  color: red;
  font-size: 20px;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error2 {
  color: rgb(128, 0, 0);
  font-size: 20px;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
